.App {
  /* text-align: center; */
  margin-bottom: 20px;
  text-transform: capitalize;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.inp {
  display: inline-block; 
  width: 45%; 
  margin-left: 10px;
  min-width: 650px;
  max-width: 800px;
}

.op {
  display: inline-block; 
  width: 50%; 
  margin-left: 10px;
  position: absolute;
  top: 80px;
  max-width: 1000px;
  min-width: 700px;
  min-height: 700px;
  background-color: rgb(238, 238, 238);
  margin-top: 20px;
}
